import html2canvas from 'html2canvas';
import * as React from 'react';
import './App.css';
import {
  SHOW_ESTIMATED_WAIT_TIME,
  SHOW_NAME,
  SHOW_QUEUE_POSITION,
  SHOW_STORE_AVERAGE_WAIT_TIME,
  SHOW_TICKET_NUMBER
} from './constants/displayElements';
import { PRINTER_PRINT_ENDPOINT } from './constants/printer';

interface IAppState {
  customerName: string,
  localCurrentDate: string
  localCurrentTime: string,
  printerIpAddress: string,
  queueName: string,
  ticketAverageWait: number,
  ticketMessage: string
  ticketMinutesLeft: number,
  ticketNumber: string,
  ticketPosition: number,
  logoURL: string,
  isLogoFooterEnabled: boolean,
  logoFooterURL: string,
  font: string,
  showAtTop: string,
  servedAtTime: string,
  venueName: string
}

class App extends React.Component<{}, IAppState> {
  private showAtTop: {};

  public constructor(props: {}) {
    super(props);

    this.state = {
      customerName: "",
      font: "",
      isLogoFooterEnabled: true,
      localCurrentDate: "",
      localCurrentTime: "",
      logoFooterURL: "",
      logoURL: "",
      printerIpAddress: "",
      queueName: "",
      servedAtTime: "",
      showAtTop: SHOW_TICKET_NUMBER,
      ticketAverageWait: 0,
      ticketMessage: "",
      ticketMinutesLeft: 0,
      ticketNumber: "",
      ticketPosition: 0,
      venueName: ""
    };

    this.showAtTop = Object.freeze({
      [SHOW_ESTIMATED_WAIT_TIME]: `${this.state.ticketMinutesLeft} min`,
      [SHOW_NAME]: this.state.customerName,
      [SHOW_QUEUE_POSITION]: this.state.ticketPosition,
      [SHOW_STORE_AVERAGE_WAIT_TIME]: `${this.state.ticketAverageWait} min`,
      [SHOW_TICKET_NUMBER]: this.state.ticketNumber
    });
  }

  public componentDidMount() {
    document.addEventListener('message', this.onMessage.bind(this));
  }

  public componentWillUnmount() {
    document.removeEventListener('message', this.onMessage.bind(this));
  }

  public render() {
    const ticketMessage = this.state.ticketMessage.replace('{customer-name}', this.state.customerName)
      .replace('{position}', this.state.ticketPosition.toString())
      .replace('{queue-name}', this.state.queueName)
      .replace('{venue-name}', this.state.venueName)
      .replace('{ticket}', this.state.ticketNumber)
      .replace('{minutes-left}', this.state.ticketMinutesLeft.toString())
      .replace('{queue-length}', (this.state.ticketPosition - 1).toString())
      .replace('{average-wait}', this.state.ticketAverageWait.toString())
      .replace('{time}', this.state.servedAtTime);

    return (
      <div>
        <button onClick={this.print.bind(this) as any}>Print</button>
        <div id="Ticket" style={{ fontFamily: this.state.font || 'Georgia', color: '#000' }}>
          <main>
            <section id="LogoContainer">
              <img id="Logo" width={180} src={this.state.logoURL as string} alt="Logo" crossOrigin="" />
            </section>
            <section id="TopContent">
              <div>
                {this.showAtTop[this.state.showAtTop]}
              </div>
            </section>
            <section id="Message">
              {ticketMessage}
            </section>
          </main>
          <footer>
            <section>
              <div>
                <div id="Footer">
                  <div id="FooterLogoContainer">
                    {this.state.isLogoFooterEnabled && <img id="FooterLogo" width={180} src={this.state.logoFooterURL as string} alt="Footer Logo" crossOrigin="" />}
                  </div>
                  <div id="FooterDateAndTimeContainer">
                    <div>
                      {this.state.localCurrentTime}
                    </div>
                    <div>
                      {this.state.localCurrentDate}
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </footer>
        </div>
      </div>
    );
  }

  private onMessage(event: MessageEvent) {
    const data = JSON.parse(event.data);

    this.setState({
      ...data
    }, () => this.print());
  }

  private print = async (): Promise<void> => {
    const url = `${this.state.printerIpAddress}${PRINTER_PRINT_ENDPOINT}`;
    const starWebPrintTrader = new StarWebPrintTrader({
      url
    });

    const element = document.getElementById('Ticket');
    const canvas = await html2canvas(element, { allowTaint: false, useCORS: true });
    const context = canvas.getContext('2d');

    if (!context) {
      return;
    }

    const builder = new StarWebPrintBuilder();
    let request = '';
    request += builder.createInitializationElement();
    request += builder
    request += builder.createBitImageElement({
      context,
      height: canvas.height,
      width: canvas.width,
      x: 0,
      y: 0,
    });
    request += builder.createCutPaperElement({ feed: true });

    starWebPrintTrader.sendMessage({ request });
  }
}

export default App;



